.wperp-filter-container h3 {
  font-size: 13px;
  margin-top: 0;
}
.wperp-filter-container .wperp-panel {
  padding: 15px;
  width: 320px;
  margin-bottom: 0;
}
.wperp-filter-container .wperp-panel .wperp-panel-body {
  padding: 15px;
  border: 1px solid #DDDDDD;
  border-radius: 3px;
  margin-bottom: 15px;
}
.wperp-filter-container .wperp-panel .wperp-panel-body * {
  font-size: 12px;
  color: #525252;
}
.wperp-filter-container .wperp-panel .wperp-panel-body .form-fields {
  display: flex;
  align-items: center;
}
.wperp-filter-container .wperp-panel .wperp-panel-body .form-fields .label-to {
  margin: 0 10px;
}
.wperp-filter-container .wperp-panel .wperp-panel-body .form-fields .has-addons {
  position: relative;
}
.wperp-filter-container .wperp-panel .wperp-panel-body .form-fields .has-addons [class*="flaticon"] {
  position: absolute;
  right: 7px;
  top: 50%;
  transform: translateY(-50%);
  pointer-events: none;
  z-index: 0;
}
.wperp-filter-container .wperp-panel .wperp-panel-body .form-field {
  width: 100%;
  max-width: 110px;
  border-radius: 3px;
  border: 1px solid #DDDDDD;
  padding: 5px;
}
.wperp-filter-container .wperp-panel .wperp-panel-footer {
  text-align: right;
}
.wperp-filter-container .wperp-panel .wperp-panel-footer .wperp-btn {
  margin-left: 10px;
}
#status_legend ul li {
  padding: 0 2px;
}
@media (max-width: 767px) {
  .wperp-filter-container {
    right: inherit !important;
    left: 0 !important;
  }
  .wperp-filter-container .wperp-panel {
    width: 290px !important;
    padding: 15px 10px;
  }
  .wperp-filter-container .wperp-panel .wperp-panel-body {
    padding: 15px 10px;
  }
  .wperp-filter-container .wperp-panel .wperp-panel-body .form-fields .label-to {
    margin: 0 5px;
  }
  .wperp-filter-container:after,
  .wperp-filter-container:before {
    right: inherit !important;
    left: 10px;
  }
}
